/* style.css */
.player-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    background-image: url('./back.png'); /* Use the provided background image */
    background-size: cover;
    background-position: center;
    min-height: 100vh;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.header p {
    font-size: 24px;
    font-weight: 400;
    width: 800px;
}
.logo {
    max-width: 200px;
}

.header-title {
    font-size: 1.5rem;
    text-align: center;
    color: #333;
}

.player-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 800px;
    padding: 20px;
    border-radius: 10px;
}

.video-player {
    width: 100%;
    max-width: 100%;
    border-radius: 10px;
    margin-bottom: 20px;
    max-height: 700px;
}

.button-group {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.target-button {
    background-color: #007BFF;
    color: white;
    border: 2px solid #007BFF;
    padding: 10px;
    margin: 5px 0;
    border-radius: 10px;
    cursor: pointer;
    font-size: 1rem;
    text-align: center;
    text-decoration: none;
}

.target-button:hover {
    background-color: #0056b3;
}

.target-button:focus {
    outline: none;
    border: 2px solid #0056b3;
}

@media (min-width: 768px) {
    .header-title {
        font-size: 2rem;
    }

    .target-button {
        font-size: 1.25rem;
    }
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.popup_inner {
    background: #fff;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
    .header-title {
        font-size: 2rem;
    }

    .target-button {
        font-size: 1.25rem;
    }
}

@media (max-width: 767px) {
    .header p {
        font-size: 1rem;
        width: 90%;
    }

    .logo {
        width: 150px;
    }

    .target-button {
        font-size: 0.875rem;
    }
    .video-player{
        max-height: 700px;
    }
}

@media (max-width: 480px) {
    .header-title {
        font-size: 1.25rem;
    }

    .target-button {
        font-size: 0.75rem;
        padding: 8px;
    }
    .video-player{
        max-height: 700px;
    }
}